.OnlineList {
    width: 100%;
    height: 100vh;
    min-width: 35rem;
    background-color: var(--color-background);
    color: var(--color-text);
    display: grid;
    grid-template-rows: 5rem auto 2.4rem; 
    overflow: hidden;
}

.OnlineList ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow-y: auto;
}