@keyframes animateIn {
    0%   {opacity: 0;}
    100% {opacity: 1;}
  }


.Backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    z-index: 500;
    animation: animateIn 0.7s ease none 1 normal;
}

.transparent {
  background-color: transparent;
}